import { Component, OnInit } from '@angular/core';
import { LoadingController, NavController, AlertController } from '@ionic/angular';
import { BasePagePage } from 'src/app/shared/base-page/base-page.page';
import { BaseService } from 'src/app/services/base.service';
import { config } from 'src/app/data-model/constant.model';

@Component({
  selector: 'app-clock-in',
  templateUrl: './clock-in.page.html',
  styleUrls: ['./clock-in.page.scss'],
})
export class ClockInPage extends BasePagePage implements OnInit {

  time: Date;
  prevNowPlaying: any;

  constructor(
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    public navCtrl: NavController,
    private service: BaseService
    ) {

      super(loadingCtrl, alertCtrl);
      
    }

  ngOnInit() {

    //get current date time, you can set your own Date() over here
    var stationdate = new Date();

    //clear Interval, it's my system requirement
    if (this.prevNowPlaying) {
      clearInterval(this.prevNowPlaying);
    }

    // set clock and Interval
    this.prevNowPlaying = setInterval(() => {
      stationdate = new Date(stationdate.setSeconds(stationdate.getSeconds() + 1));
      this.time = stationdate;
    }, 1000);

  }

  onMachineDownClicked(form, e: Event) {

    config.log('timeeee: ', this.time);

    this.service.setData('downtime', this.time);

    this.navCtrl.navigateForward('/home/machine-down/downtime');

  }

}
